/* media query mixin
===================== */
@mixin minMediaQuery($width) {
    @media screen and (min-width: $width) {
        @content;
    }
}
@mixin maxMediaQuery($width) {
    @media screen and (max-width: $width) {
        @content;
    }
}
@font-face {
    src: url(../webfonts/fa-solid-900.ttf);
    font-family: "fontawesome";
}
@font-face {
    src: url(https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap);
    font-family: "Rubik Mono One", sans-serif;
}
body {
    overflow-x: hidden;
    // font-family: Lato;
    // height: 100vh;
    font-family: "Rubik Mono One", sans-serif;
}
button:focus {
    box-shadow: none !important;
}
.text-facebook {
    color: #4267b2;
}
.text-twitter {
    color: #1da1f2;
}
.text-instagram {
    color: transparent;
    background: rgba(255, 219, 128, 1);
    background-image: linear-gradient(
        45deg,
        rgba(255, 219, 128, 1) 0%,
        rgba(252, 176, 69, 1) 10%,
        rgba(247, 118, 54, 1) 18%,
        rgba(245, 99, 66, 1) 28%,
        rgba(253, 28, 28, 1) 39%,
        rgba(226, 50, 108, 1) 49%,
        rgba(193, 68, 130, 1) 62%,
        rgba(132, 59, 181, 1) 75%,
        rgba(88, 81, 219, 1) 88%,
        rgba(65, 79, 230, 1) 100%
    );
    background-clip: text;
    -webkit-background-clip: text;
}

/*---------------------------------------------------------------------------------------------*/
/*--------------------------------------------header ------------------------------------------*/
header.top-header {
    // background: #607d8b;
    background: #4d7184;
    h1 {
        color: rgb(224, 224, 224);
        span.job-title {
            font-size: 1.2rem;
            letter-spacing: 0.035rem;
        }
    }
    address {
        color: #d2d6e0;
        a {
            color: #7eb6f3;
        }
    }
}
/*---------------------------------------------------------------------------------------------*/
section {
    font-size: 0.92rem;
    font-family: Lato;
    header h2 {
        font-size: 2rem;
        font-weight: 600;
        text-transform: uppercase;
    }
    h3 {
        font-size: 1.25rem;
        font-weight: 500;
    }
    &.experience {
        h3 a {
            margin-left: 1rem;
            font-size: 1rem;
            align-self: center;
        }
    }
}
